@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  font-family: "Bebas Neue", "Poppins", sans-serif;
  color: #1b1b1e;
}

body {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  background-color: #d8115a;
}

.btn {
  display: inline-block;
  background: #1b1b1e;
  color: #d81159;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.App {
  width: 100vw;
  height: 100vh;

}



.Start {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  z-index: 10;
  padding: 0;
}

.Start > strong {
  margin-top: 25vh;
  font-size: 13vw;
}

.Start > p {
  margin-top: -2vw;
}

.Start > button {
  margin-top: 15vh;
  color: #d81159;
  font-size: 3vw;
}

@media (max-width: 414px) {
  .Start > strong {
    margin-top: 25vh;
    font-size: 20vw;
  }

  .Start > p {
    font-size: 5vw;
  }

  .Start > button {
    font-size: 6vw;
  }
}

.ShipPlacement {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
}

.ShipPlacement_container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
  margin-top: 5vh;
}

.ShipSelection {
  min-width: 300px;
  margin: 10px;
}

.selections {
  display: inline-block !important;
}

.DropBoard {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: auto;
  justify-items: center;
  grid-gap: 5px;
  align-items: center;
  padding: 15px;
  background-color: #1b1b1e;
  box-shadow: 0px 0px 5px rgb(0, 0, 0);
}

.DropPanel {
  display: inline-block;
  border: none;
  width: 35px;
  height: 35px;
  background-color: rgb(13, 198, 255);
  text-decoration: none;
  font-size: 24px;
  font-family: Arial, Helvetica, sans-serif;
}
.draggingOver {
  background-color: red;
}

.dropped {
  background-color: #3aff13;
}

.ShipSelection {
  display: flex;
  flex-flow: column;
  align-items: flex-start;
}

.DragShip {
  display: flex;
  flex-flow: row;
  margin: 10px 5px;
  color: white;
}

.vertical {
  flex-flow: column;
}

.selectShip {
  background-color: #3aff13;
  width: 40px;
  height: 40px;
  border: 2.5px solid #1b1b1e;
}

.ShipPlacement > p {
  margin-top: 5vh;
}

.btn_disabled {
  background-color: rgb(73, 73, 73);
  color: rgb(192, 192, 192);
}

.btn_disabled:active {
  transform: scale(1);
}

@media (max-width: 440px) {
  .DropPanel {
    width: 21px;
    height: 21px;
  }

  .selectShip {
    width: 26px;
    height: 26px;
  }
}

.Game {
  padding-left: 1vw;
  padding-right: 1vw;
  padding-bottom: 3vh;
}

.Restart {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 15;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  bottom: 0;
  margin: auto;
}

.Restart_container {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  border: 5px solid #1b1b1e;
  border-radius: 10px;
  background-color: #d81159;
  text-align: center;
  padding: 5vw;
  box-shadow: 0px 0px 5px black;
}

.logo {
  padding: 10px;
  text-align: center;
}

.btn_logo {
  font-size: 36px;
  border: none;
  background-color: #d81159;
}

.status_container {
  display: flex;
  justify-content: center;
  margin: 20px;
}

.status_box {
  border: 5px solid #1b1b1e;
  padding: 10px;
  text-align: center;
  font-size: 24px;
  min-width: 200px;
}

.board_container {
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
}

.player_container,
.cpu_container {
  text-align: center;
}

.board {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: auto;
  justify-items: center;
  grid-gap: 5px;
  align-items: center;
  padding: 15px;
  background-color: #1b1b1e;
  box-shadow: 0px 0px 5px rgb(0, 0, 0);
}

.board.cpu_board {
  background-color: #1b1b1e;
}

.Panel {
  display: inline-block;
  border: none;
  width: 35px;
  height: 35px;
  background-color: rgb(13, 198, 255);
  text-decoration: none;
  font-size: 24px;
  font-family: Arial, Helvetica, sans-serif;
}

.cpu_board .Panel {
  cursor: pointer;
}

.cpu_board .Panel:hover {
  background-color: rgb(255, 171, 14);
}

.Panel:active {
  transform: scale(0.98);
}

.ship {
  background-color: #3aff13;
}

.hit {
  color: red;
}

.miss {
  color: black;
  font-size: 14px;
}

.sunk {
  color: rgb(255, 65, 65);
  background-color: rgb(37, 37, 37);
}

/*icons*/
.fa-times-circle {
  color: rgb(255, 65, 65);
}

@media (max-width: 1115) {
  .board_container {
    flex-flow: column;
  }
}

@media (max-width: 440px) {
  .Panel {
    width: 21px;
    height: 21px;
    font-size: 16px;
  }
}

.Result {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 20;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  bottom: 0;
  margin: auto;
}

.Result_container {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  border: 5px solid #1b1b1e;
  border-radius: 10px;
  background-color: #d81159;
  text-align: center;
  padding: 5vw;
}

.Result_container > strong {
  font-size: 10vw;
}

.Result_container > button {
  font-size: 2vw;
  color: #d81159;
}

@media (max-width: 900px) {
  .Result_container > button {
    font-size: 3vw;
  }
}

@media (max-width: 600px) {
  .Result_container > button {
    font-size: 4vw;
  }
}

@media (max-width: 450px) {
  .Result_container > button {
    font-size: 5vw;
  }
}

